body {
  height: 100%;
  color: var(--color-black);
  background-color: var(--color-white);
  margin: 0;
  font-family: Montserrat Alternates, Inter, sans-serif;
  font-weight: 300;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

p:last-child {
  margin-bottom: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

button {
  cursor: pointer;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

svg {
  width: 0;
  height: 0;
}

* {
  box-sizing: border-box;
}

.container-index {
  min-width: 320px;
  margin: 0 auto;
  padding: 0 13px;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

:root {
  --main-dark-grey: #1c1c1c;
  --light-grey: rgba(28, 28, 28, .2);
  --main-blue: #4753dc;
  --light-blue: #f1f2fe;
  --yellow: #ffdc75;
  --white: #fff;
  --main-trans: 250ms cubic-bezier(.4, 0, .2, 1);
}

.header-container {
  justify-content: space-between;
  align-items: center;
  padding-top: 22px;
  padding-bottom: 17px;
  padding-right: 24px;
  display: flex;
}

.header-logo-link {
  width: 49px;
  height: 27px;
  margin-left: 11px;
  margin-right: 42px;
}

.icon-logo-mob {
  width: 49px;
  height: 27px;
  stroke-width: 0;
  stroke: currentColor;
  fill: currentColor;
  display: inline-block;
}

.icon-logo-pc {
  display: none;
}

@media screen and (max-width: 389px) {
